/* icon
-------------------------- */
.field-select {
  padding: 0;
  width: 100%;
  display: block;
  height: 100%;
  position: relative;
  border: 1px solid transparent;
}
.field-select .slide-up-enter-active,
.field-select .slide-up-leave-active {
  -webkit-transition: all 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  transition: all 0.3s cubic-bezier(0.23, 1, 0.32, 1);
}
.field-select .slide-up-enter,
.field-select .slide-up-leave-active {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
  opacity: 0;
}
.field-select.show {
  border-color: #eee;
}
.field-select .field-select-block {
  height: 100%;
}
.field-select .field-select-block input {
  height: 100%;
  padding: 0 8px;
}
.field-select .field-select-box {
  position: absolute;
  left: 0;
  top: 36px;
  background-color: white;
  list-style: none;
  margin: 0;
  padding: 0;
  border: 1px solid #EFF2F7;
  -webkit-box-shadow: 0 10px 15px 1px #eee;
          box-shadow: 0 10px 15px 1px #eee;
  max-height: 200px;
  overflow-y: auto;
  width: 100%;
}
.field-select .field-select-box .field-option {
  cursor: pointer;
  border-bottom: 1px solid #EFF2F7;
  height: 36px;
  line-height: 36px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 8px;
}
.field-select .field-select-box .field-option.active,
.field-select .field-select-box .field-option:hover {
  background-color: #F9FAFC;
}



