/* Colors
-------------------------- */
/* icon
-------------------------- */
.field-check-box {
  height: 35px;
  text-align: center;
}
.field-check-box.show {
  border-color: red;
}
.field-check-box .hide-checkbox {
  padding: 0;
  margin: 0;
  width: 0;
  height: 0;
}
.field-check-box span.k-check-box {
  vertical-align: middle;
  display: inline-block;
  margin-top: 5px;
  width: 25px;
  height: 25px;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: transparent;
  cursor: pointer;
  border: 1px solid #C0CCDA;
  border-radius: 3px;
}
.field-check-box span.k-check-box.checked {
  border: 1px solid #00CBC7;
  background-color: #00CBC7;
  color: white;
}
.field-check-box span.k-check-box .icon {
  color: white;
  width: 25px;
  height: 25px;
}



