/* Colors
-------------------------- */
/* icon
-------------------------- */
.field-query {
  width: 100%;
  height: 100%;
  position: relative;
}
.field-query .field-query-block {
  height: 100%;
}
.field-query .field-query-block input {
  height: 100%;
  padding: 0 8px;
}
.field-query .select-box {
  position: absolute;
  left: 0;
  background-color: white;
  width: auto;
  overflow-x: auto;
  list-style: none;
  margin: 0;
  border: 1px solid #EFF2F7;
  -webkit-box-shadow: 0 10px 15px 1px #eee;
          box-shadow: 0 10px 15px 1px #eee;
}
.field-query .select-box .select-box-table tr {
  cursor: pointer;
}
.field-query .select-box .select-box-table tr td,
.field-query .select-box .select-box-table tr th {
  padding: 0 8px;
}
.field-query .select-box .select-box-table tr:hover {
  background-color: #F9FAFC;
}



