.knife-field {
  height: 100%;
  width: 100%;
}
.field-box {
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.field-box input {
  height: 35px;
  line-height: 35px;
  width: 100%;
  padding: 0 8px;
  font-size: 14px;
}
.label-icon {
  width: 36px;
  height: 100%;
  color: #475669;
  background-color: white;
  cursor: pointer;
}
.label-icon:hover {
  background-color: #F9FAFC;
}



